import { backendLookup } from '../lookup'

export const apiTweetList = (callback,username) => {
    let endpoint = '/'
    if (username) {
        endpoint = `/?username=${username}`
    }
    backendLookup('GET', endpoint, callback)
}

export const apiTweetDetail = (callback, tweetId) => {
    backendLookup('GET', `/${tweetId}`, callback)
}


export const apiTweetCreate = (newTweet, callback) => {
    const data = { 'content': newTweet }
    backendLookup('POST', '/create/', callback, data)
}

export const apiTweetAction = (tweetId, action, callback) => {
    const data = { 'id': tweetId, 'action': action }
    backendLookup('POST', '/action/', callback, data)
}