import React, { useState } from 'react';
import { ActionBtn } from './buttons';



export const Tweet = ({ tweet, setNewTweet, parent }) => {
    const [didPerformAction, setDidPerformAction] = useState(true)
    var match = window.location.pathname.match(/(?<tweetId>\d+)/)
    var tweetIdPath = match ? match.groups.tweetId : null
    const isDetail = tweetIdPath && tweetIdPath === `${tweet.id}` ? true : false


    return <div style={{ border: '1px solid black', marginBottom: 20 }}>
        {tweet.id} - {tweet.content}
        {parent
            ? null
            : <>
                {didPerformAction
                    ? <ActionBtn setDidPerformAction={setDidPerformAction} tweet={tweet} action='like' />
                    : <ActionBtn setDidPerformAction={setDidPerformAction} tweet={tweet} action='unlike' />}
                <ActionBtn tweet={tweet} setNewTweet={setNewTweet} action='retweet' />
            </>}
        {isDetail ? null : <a href={`/${tweet.id}`}>Подробнее</a>}
        {tweet.parent && <div style={{ backgroundColor: '#44ffa2' }}><Tweet tweet={tweet.parent} parent={tweet.parent} setNewTweet={setNewTweet} /></div>}
    </div>
}
