import React, { useState } from 'react';
import { apiTweetAction } from '../lookup';

export const ActionBtn = ({ tweet, action, setNewTweet, setDidPerformAction }) => {
    const [numberOfLikes, setNumberOfLikes] = useState(tweet.likes)

    const handleClick = (event) => {
        event.preventDefault()
        apiTweetAction(tweet.id, action, (response, status) => {
            if (status === 200) {
                setNumberOfLikes(response.likes)
                setDidPerformAction(didPerformAction => !didPerformAction)
            }
            else if (status === 201) setNewTweet(response)
            else alert('Что-то пошло не так. Попробуйте позже')
        })
    }

    switch (action) {
        case 'like':
            return <button onClick={(event) => handleClick(event)}>{numberOfLikes} / Like</button>
        case 'unlike':
            return <button onClick={(event) => handleClick(event)}>{numberOfLikes} / Unlike</button>
        case 'retweet':
            return <button onClick={(event) => handleClick(event)}>Retweet</button>
        default: return null
    }
}
